<template>
    <div class="image-input">
        <img style="float:left" :src="base64img" />
        <label style="height:100%; float:left; margin-left:10px">
            {{title}}
            <input type="file" :accept="getFileTypes()" @change="uploadImage($event)" id="file-input">
        </label>
    </div>
</template>
<script>

export default {
    model: {
        prop: 'imageData',
        event: 'change'
    },
    props: {
        imageData: null,
        default: null
    },
    data() {
        return {
            fileTypes: ['image/png', 'image/gif', 'image/svg+xml', 'image/jpg', 'image/jpeg'],
            title: "Format carré. max 20Ko",
            base64img: null
        }
    },
    methods: {
        getFileTypes() {
            return(this.fileTypes.join(', '))
        },
        uploadImage(event) {
            const input = event.target
            const files = input.files
            if (files && files[0]) {
                if (this.fileTypes.includes(files[0].type)) {
                    this.$emit('change', files[0])
                    
                    const reader = new FileReader
                    reader.onload = e => {
                        this.base64img = e.target.result
                        this.title=files[0].name
                    }
                    reader.readAsDataURL(files[0])
                }
            }
        },
        resizeImg(e, file) {
            var image = new Image();
            image.src = e.target.result;

            image.onload = function() {
                var maxWidth = 1024,
                    maxHeight = 1024,
                    imageWidth = image.width,
                    imageHeight = image.height;

                if (imageWidth > imageHeight) {
                    if (imageWidth > maxWidth) {
                        imageHeight *= maxWidth / imageWidth;
                        imageWidth = maxWidth;
                    }
                }
                else {
                    if (imageHeight > maxHeight) {
                        imageWidth *= maxHeight / imageHeight;
                        imageHeight = maxHeight;
                    }
                }

                var canvas = document.createElement('canvas');
                canvas.width = imageWidth;
                canvas.height = imageHeight;
                image.width = imageWidth;
                image.height = imageHeight;
                var ctx = canvas.getContext("2d");
                ctx.drawImage(this, 0, 0, imageWidth, imageHeight);

                this.imageData = canvas.toDataURL(file.type);
            }
        }
    }
}
</script>
