<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Editer "{{getPerson.name}}"</h1>
          <label>Nom: <input v-model="person.name"></label><br>
          <label>Image: <imageInput v-model="person.img" /></label><br>
          <!-- <label>Image: <input @change="loadImg" type="file"></label><br> -->
          <label>Date: <input v-model="person.date"></label>
          <h3>Biographie</h3>
          <editor v-model="person.bio"
          :options="defaultOptions"
          height="700px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="bio"/>
          <h3>Notes</h3>
          <editor v-model="person.notes"
          :options="defaultOptions"
          height="300px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="notes"/>
          <StateSelector v-model="person.state"/>
          <input type="button" value="Annuler" @click="$router.push($route.path.replace('/edit',''))">
          <input type="button" value="Enregistrer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import StateSelector from '@/components/state/Selector.vue'
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'
import ImageInput from '@/components/imageInput.vue'

export default {
  name: 'home',
  components: {
    StateSelector,
    Editor,
    ImageInput
  },
  data () {
    return {
        person: {},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchPerson(this.$route.params.person)
    .then(()=>{
        this.person = {...this.getPerson}
        delete this.person.img
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons',[
        'getPerson',
        'getPersons',
        'personLoading'
        ])
  },
  methods: {
    ...mapActions('persons',[
      'fetchPerson',
      'setPersons',
      'updatePerson'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    loadImg(e) {
      this.person.img = e.target.files[0]
    },
    save() {
      this.loading = true;
      let form = new FormData();
      form.append("slug", this.getPerson.slug)
      form.append("name", this.person.name)
      form.append("state", this.person.state)
      if(this.person.img) {
        form.append("img", this.person.img)
      }
      form.append("date", this.person.date)
      form.append("bio", this.$refs.bio.invoke('getMarkdown'))
      form.append("notes", this.$refs.notes.invoke('getMarkdown'))
        this.updatePerson(form)
            .then(r=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.setPersons(r.data);
                this.$router.push('/'+this.getPerson.slug)
            })
            .catch(e=>{
                this.loading = false
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
